import { createStore } from 'redux';
import { show } from './atoms/json/company'

const initData = {
  company : show()
}

// ã¬ãã¥ã¼ãµã¼
export function companyReducer(state = initData, action) {
  switch (action.type) {
    case 'ALL' :
      return viewReducer(state, action);
    case 'FIND' :
      return findReducer(state, action);
    default :
      return state;
  }
}

// åæè¡¨ç¤ºã®ã¬ãã¥ã¼ã¹å¦ç
function viewReducer(state, action) {
  return {
    company : []
  }
}
// ã«ãã´ãªæ¤ç´¢ã®ã¬ãã¥ã¼ã¹å¦ç
function findReducer(state, action) {

  let key = action.find;
  let companys = show();
  let fdata = [];

  if (key !== 'all') {
    companys.forEach((value) => {
      if (value.category.indexOf(key) >= 0) {
        fdata.push(value);
      }
    });
  } else {
    fdata = show();
  }
  return {
    company : fdata,
  };
}

// åæè¡¨ç¤ºã®ã¢ã¯ã·ã§ã³
export function view() {
  return {
    type : 'ALL',
  }
}
// çµãè¾¼ã¿ã®ã¢ã¯ã·ã§ã³
export function find(key) {
  return {
    type : 'FIND',
    find : key
  }
}

// ã¹ãã¢ä½æ
export default createStore(companyReducer);